	module.exports = {"data":{"markdownRemark":{"html":"<h2>Range-based for loop</h2>\n<p>The ranged-based <code>for</code> loop allows easy looping over a range of elements (like elements in a container).</p>\n<p>With traditional <code>for</code> loop:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-cpp\"><code>std<span class=\"token operator\">::</span>vector<span class=\"token operator\">&lt;</span>std<span class=\"token operator\">::</span>string<span class=\"token operator\">></span> stringList <span class=\"token punctuation\">{</span><span class=\"token string\">\"one\"</span><span class=\"token punctuation\">,</span> <span class=\"token string\">\"two\"</span><span class=\"token punctuation\">,</span> <span class=\"token string\">\"three\"</span><span class=\"token punctuation\">}</span><span class=\"token punctuation\">;</span>\n\n<span class=\"token keyword\">for</span> <span class=\"token punctuation\">(</span>size_t il<span class=\"token punctuation\">;</span> il <span class=\"token operator\">&lt;</span> stringList<span class=\"token punctuation\">.</span><span class=\"token function\">size</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span> il<span class=\"token operator\">++</span>\n<span class=\"token punctuation\">{</span>\n  std<span class=\"token operator\">::</span>cout <span class=\"token operator\">&lt;&lt;</span> stringList<span class=\"token punctuation\">.</span><span class=\"token function\">at</span><span class=\"token punctuation\">(</span>il<span class=\"token punctuation\">)</span> <span class=\"token operator\">&lt;&lt;</span> std<span class=\"token operator\">::</span>endl<span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<p>With range-based <code>for</code> loop:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-cpp\"><code>std<span class=\"token operator\">::</span>vector<span class=\"token operator\">&lt;</span>std<span class=\"token operator\">::</span>string<span class=\"token operator\">></span> stringList <span class=\"token punctuation\">{</span><span class=\"token string\">\"one\"</span><span class=\"token punctuation\">,</span> <span class=\"token string\">\"two\"</span><span class=\"token punctuation\">,</span> <span class=\"token string\">\"three\"</span><span class=\"token punctuation\">}</span><span class=\"token punctuation\">;</span>\n\n<span class=\"token keyword\">for</span> <span class=\"token punctuation\">(</span><span class=\"token keyword\">auto</span><span class=\"token operator\">&amp;</span> singleString <span class=\"token operator\">:</span> stringList<span class=\"token punctuation\">)</span>\n<span class=\"token punctuation\">{</span>\n  std<span class=\"token operator\">:</span>cout <span class=\"token operator\">&lt;&lt;</span> singleString <span class=\"token operator\">&lt;&lt;</span> std<span class=\"token operator\">::</span>endl<span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>","fields":{"slug":"/cplusplus/rangeforloop/"},"frontmatter":{"title":"Range For Loop"}}},"pathContext":{"slug":"/cplusplus/rangeforloop/"}}

